import React from 'react';
import {Link} from 'react-router-dom';

const AmazingFeatureCard = (props) => {
     return (
        <React.Fragment>
        <div className="col-lg-6 col-md-6">
        <div className="single-feature landscape">
            <div className={props.NameClass}>
                <img  src={props.imgAmazingName} alt="" />
            </div>
            <div className="content">
                <h4 className="title">
                   {props.title}
                </h4>
                <Link to={props.amazingReadMoreLink} className="link">read more <i className="fas fa-arrow-right"></i></Link>
            </div>
        </div>
    </div>    
        </React.Fragment>
     )

}

export default AmazingFeatureCard;