import React from 'react'
import { Link} from 'react-router-dom';

const PlayersCard = (props) => {
   return (
       <React.Fragment>
       <div class="single-winer">
       <div class="top-area">
           <div class="left">
               <h4 class="name">
                      {props.Playername}
               </h4>
               <p class="date">
                     {props.winDate}
               </p>
           </div>
           <div class="right">
               <p class="id">{props.playerRegNo}</p>
           </div>
       </div>
       <div class="bottom-area">
           <div class="left">
                 <Link style={{fontSize:'18px',color:'#f0ad4e'}} to={props.btnReadMoreName}> {props.btnReadMoreName}</Link>
           </div>
           <div class="right">
                   <img src={props.Winphoto} alt="" />
           </div>
       </div>
   </div>
       </React.Fragment>
   )
}
export default PlayersCard;